import {React,useState, useEffect} from 'react'
import './Home.scss'
import { Header } from '../components/Header'
import axios from 'axios'
import { url } from '../const'
import { useCookies } from 'react-cookie'
import ReactPaginate from 'react-paginate'
import { useNavigate } from 'react-router-dom'

export function Home () {
    const navigate = useNavigate()
    const createReview=()=>{
      navigate('/React.js3/new')
    }

    return(
        <div>
            <Header/>
            <main className="reviewList">
                <h1 className="reviewList-title"></h1>
                <button onClick={createReview} className='createReview-button'></button>
                <div className="reviews">
                  <Reviews/>
                </div>
            </main>
        </div>
    )
}

export const Reviews =()=>{
  const [lists, setLists] = useState([])
  const [cookies] = useCookies()
  const [errorMessage, setErrorMessage] = useState('')
  const navigate = useNavigate()
  useEffect(() => {
    axios
      .get(`${url}books?offset=0`, {
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setLists(res.data)
      })
      .catch((err) => {
        setErrorMessage(`${err}`)
      })
},[])

  // function
  const handlePageChange = (data) => {
    console.log(data)
    let page_number = data['selected']; // {selected: 2}
    console.log(page_number)
    const offset=(page_number)*10; // offset
    axios
      .get(`${url}books?offset=${offset}`, {
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setLists(res.data)
      })
      .catch((err) => {
        setErrorMessage(`${err}`)
      })
  }

  const goReview =(e)=>{
    axios  
      .get(`${url}books/${e.currentTarget.id}`,{
        headers:{
          authorization:`Bearer ${cookies.token}`,
        },
      })
      .then((res)=>{
        console.log(res.data)
        if (res.data.isMine){
          navigate(`/React.js3/edit/${res.data.id}`,{state:{reviewData:res.data}})
        }
        else{
          navigate(`/React.js3/detail/${res.data.id}`,{state:{reviewData:res.data}})
        }
        axios
            .post(`${url}logs`,{selectBookId:res.data.id},{
              headers:{
                authorization:`Bearer ${cookies.token}`,
              },
            })
      })
  }

  return (
    <>
      <ul>
          <p className="error-message">{errorMessage}</p>
          {lists.map((list,key)=>{
            return(
              <div className="review-items" key={key} id={list.id} onClick={goReview}>
                <h2 className="review-items__title">{list.title}</h2>
                <p className="review-items__url">URL{list.url}</p>
                <h4 className="review-items__reviewer">{list.reviewer}</h4>
                <h3 className='review-items__review'>:{list.review}</h3>
              </div>
            )
          })}
      </ul>
      {/*  */}
      <ReactPaginate
        previousLabel={'<'}
        nextLabel={'>'}
        breakLabel={'...'}
        pageCount={10} // 
        marginPagesDisplayed={1} // 
        pageRangeDisplayed={3} // 
        onPageChange={handlePageChange} // function
        containerClassName={'pagination'} // ul
        subContainerClassName={'pages pagination'}
        activeClassName={'active'} // li
        previousClassName={'pagination__previous'} // <li
        nextClassName={'pagination__next'} // >li
        disabledClassName={'pagination__disabled'} // <,>
      />
    </>
  )
}
